import { NextResponse, type NextRequest } from 'next/server'

export function middleware(request: NextRequest) {
  // ðŸŽ¯ Allow public access to user dashboard for demos
  if (request.nextUrl.pathname.startsWith('/user')) {
    return NextResponse.next()
  }

  // Check for auth token in cookies (simple approach)
  const authToken = request.cookies.get('sb-access-token') || 
                   request.cookies.get('sb-iiiciapavjonpmldytxf-auth-token')

  // Public routes that don't need auth
  const publicRoutes = ['/', '/auth', '/onboarding']
  const isPublicRoute = publicRoutes.some(route => 
    request.nextUrl.pathname === route || 
    request.nextUrl.pathname.startsWith(route + '/')
  )

  // If no auth token and trying to access protected route
  if (!authToken && !isPublicRoute) {
    const url = request.nextUrl.clone()
    url.pathname = '/auth/login'
    return NextResponse.redirect(url)
  }

  return NextResponse.next()
}

export const config = {
  matcher: [
    /*
     * Match all request paths except:
     * - _next/static (static files)
     * - _next/image (image optimization files)
     * - favicon.ico (favicon file)
     * - images - .svg, .png, .jpg, .jpeg, .gif, .webp
     * Feel free to modify this pattern to include more paths.
     */
    '/((?!_next/static|_next/image|favicon.ico|.*\\.(?:svg|png|jpg|jpeg|gif|webp)$).*)',
  ],
}
